@charset "utf-8";
@import '../setting/color';
.message-body {
  writing-mode: vertical-rl;
  height: 721px;
  padding-top: 64px;
  padding-bottom: 120px;
  margin: 0 auto;
  text-orientation: upright;
  letter-spacing: 0px;
  @include mq(600) {
    padding-bottom: 30px;
    height: 550px;
  }
}
.message-ttl {
  height: auto;
  @include font-size(28);
  font-family: $font-base;
  letter-spacing: 0px;
  color: $color02;
  line-height: 33px;
  letter-spacing: 14px;
  &-indent {
    text-indent: 18%;
  }
  @include mq(600) {
    @include font-size(20);
    line-height: 25px;
  }
}
.message-txt {
  @include font-size(16);
  font-family: $font-base;
  letter-spacing: 0px;
  color: $color02;
  line-height: 35px;
  letter-spacing: 6px;
  text-indent: 18%;
  @include mq-m(375, 600) {
    @include font-size(12);
    line-height: 20px;
  }
  @include mq(374) {
    @include font-size(12);
    line-height: 14px;
  }
}
.message-store-name {
  @include font-size(22);
  font-family: $font-base;
  letter-spacing: 10px;
  color: $color02;
  @include mq(600) {
    @include font-size(20);
  }
  &-indent {
    text-indent: 54%;
  }
}
@include mq(900) {
  .tablet {
    display: none;
  }
}
