@charset "utf-8";

.header-nav {
  width: 192px;
  height: 100%;
  margin-left: 6.4%;
  @include mq(767) {
    display: none;
  }
  &-list {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-item {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-link {
    color: $color01;
    @include font-size(16);
    font-family: $font-base;
    &.black {
      color: $color02;
    }
  }
}
