@charset "utf-8";
@import '../setting/color';
.info-background {
  background-image: url(../../img/Medium_bg.jpg);
}
.access-heading {
  @include font-size(36);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  text-align: center;
  @include mq(767) {
    @include font-size(24);
  }
  &__layout {
    margin-top: 20px;
    margin-bottom: 84px;
  }
}
.access-content {
  display: flex;
  justify-content: center;
  align-items: center;
  @include mq(767) {
    display: block;
  }
}
.access-img-wrapper {
  @include mq(1024) {
    width: 90%;
  }
  @include mq(767) {
    width: 100%;
  }
}
.access-img-wrapper img {
  width: 656px;
  height: 370px;
  @include mq(1024) {
    width: 100%;
    object-fit: cover;
  }
  @include mq(767) {
    padding-right: 2vw;
    padding-left: 2vw;
  }
}
.access-body {
  margin-left: 83px;
  @include mq(767) {
    margin-top: 20px;
    margin-left: 0;
    padding-right: 15vw;
    padding-left: 15vw;
  }
}
.access-ttl {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: bold;
}
.access-txt {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  &__layout {
    margin-bottom: 16px;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}
.google-map-wrapper {
  width: 792px;
  height: 592px;
  margin: 69px auto 0;
  @include mq(840) {
    width: 100%;
    height: 400px;
    padding-right: 2vw;
    padding-left: 2vw;
  }
}
