@charset "utf-8";
@import '../setting/color';
.pan-list-wrapper {
  max-width: 1180px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 36px;
  @include mq(1200) {
    padding-left: 50px;
  }
  @include mq(767) {
    margin-top: 10px;
    padding-left: 20px;
  }
}
.pan-list {
  display: flex;
}
.pan-list-txt {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(767) {
    @include font-size(12);
  }
  &__layout {
    margin-right: 8px;
    & p:last-child {
      margin-right: 0;
    }
  }
}
.fa-angle-right {
  @include font-size(16);
  color: $color02;
  margin-right: 8px;
  margin-top: 8px;
}
