@charset "utf-8";
.g-btn {
  display: inline-block;
  width: 200px;
  height: 56px;
  line-height: 56px;
  border: 1px solid #707070;
  text-align: center;
  font-family: $font-base;
  @include font-size(16);
  color: $color02;
  background-color: $color01;
  &:focus,
  &:hover {
    color: $color01;
    background-color: $color02;
  }
}
.reservation-form-btn {
  @include font-size(16);
  line-height: 42px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  border: 1px solid #707070;
  background-color: #fff;
  &:focus,
  &:hover {
    color: $color01;
    background-color: #707070;
    border: 1px solid #fff;
  }
  &__layout {
    display: block;
    margin-right: auto;
    margin-left: auto;
    width: 198px;
    height: 42px;
    text-align: center;
    margin-top: 64px;
  }
}
