@charset "utf-8";
$hamburger-color01: #fff;
$hamburger-color02: #000;

.hamburger {
  display: none;
}
@include mq(767) {
  .button {
    display: inline-block;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }

  .hamburger {
    display: block;
    position: fixed;
    // ハンバーガーメニューの位置
    top: 25px;
    right: 15px;
    z-index: map-get($layer, 'drawer') + 1;
    // ハンバーガーメニューの大きさ
    width: 22px;
    height: 10px;
    // border-radius: 50%;
    // border: 3px solid $hamburger-color01;
    // border: 3px solid $hamburger-color02;
    // box-shadow: 0 0 2rem transparent;
    outline: none;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    // &:hover,
    // &:focus {
    //   box-shadow: 0 0 0.5rem rgba(161, 161, 161, 0.5);
    //   transition: 0.3s;
    // }
  }
  // ----- ハンバーガーメニュー周辺文字 -----//
  .hamburger-label {
    position: absolute;
    top: 16px;
    right: -6px;
    @include font-size(10);
    font-family: 'Montserrat ExtraBold';
    color: $hamburger-color01;
    // color: $hamburger-color02;
    &.black {
      color: $hamburger-color02;
    }
  }
  // ----- ハンバーガーボタンのライン -----//
  .hamburger__line {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 22px;
    height: 2px;
    background-color: $hamburger-color01;
    // background-color: $hamburger-color02;
    -webkit-transition: inherit;
    transition: inherit;
    &.black {
      background-color: $hamburger-color02;
    }
    &::before,
    &::after {
      position: absolute;
      display: block;
      width: 100%;
      height: 100%;
      background-color: inherit;
      content: '';
      -webkit-transition: inherit;
      transition: inherit;
    }
  }

  .hamburger__line::before {
    top: -5px;
  }
  .hamburger__line::after {
    top: 5px;
  }
  // .hamburger[aria-expanded='true'] {
  //   border: 2px solid #fff !important;
  // }
  .hamburger[aria-expanded='true'] .hamburger__line {
    background-color: transparent;
    &::before,
    &::after {
      top: 0;
      background-color: $hamburger-color01;
      // background-color: $hamburger-color02;
      &.black {
        background-color: $hamburger-color02;
      }
    }
  }
  .hamburger[aria-expanded='true'] .hamburger__line::before {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  .hamburger[aria-expanded='true'] .hamburger__line::after {
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
}
