@charset "utf-8";
@import '../setting/color';
.feature-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 122px;
  @include mq(1024) {
    display: block;
    padding-right: 4vw;
    padding-left: 4vw;
  }
  &:nth-of-type(2n) {
    flex-direction: row-reverse;
  }
  &:last-of-type {
    margin-bottom: 0;
  }
}
.feature-img-wrapper {
  flex: 0 1 54.6875%;
  @include mq-m(1025, 1700) {
    flex: 0 1 55%;
  }
  @include mq(1024) {
    display: block;
  }
}
.feature-body {
  flex: 1;
  &__layout {
    margin-left: 7.1354%;
    padding-top: 53px;
    padding-bottom: 108px;
    padding-right: 4vw;
    @include mq-m(1025, 1399) {
      padding-bottom: 38px;
    }
    @include mq(1024) {
      padding-bottom: 0;
      margin-right: 7.1354%;
    }
  }
}
.feature-body__reverse {
  &__layout {
    padding-top: 53px;
    padding-bottom: 108px;
    padding-left: 4vw;
    margin-right: 7.1354%;
    @include mq-m(1025, 1700) {
      margin-right: 7.063%;
    }
    @include mq(1024) {
      padding-bottom: 0;
      margin-right: 7.1354%;
    }
  }
}
.feature-ttl {
  @include font-size(34);
  line-height: 62px;
  color: $color02;
  font-family: $font-base;
  letter-spacing: 0px;
  @include mq-m(768, 1399) {
    @include font-size(24);
    line-height: 35px;
  }
  @include mq(767) {
    @include font-size(20);
    line-height: 30px;
  }
  &__layout {
    margin-bottom: 75px;
    @include mq(1399) {
      margin-bottom: 30px;
    }
  }
}
.feature-ttl__reverse {
  &__layout {
    margin-bottom: 75px;
    @include mq(1399) {
      margin-bottom: 30px;
    }
  }
}
.feature-txt {
  @include font-size(16);
  line-height: 32px;
  color: $color02;
  font-family: $font-base;
  letter-spacing: 0px;
  &__layout {
    margin-bottom: 75px;
    @include mq(1399) {
      margin-bottom: 30px;
    }
  }
}
.feature-txt__reverse {
  &__layout {
    margin-bottom: 75px;
    @include mq(1399) {
      margin-bottom: 30px;
    }
  }
}
.feature-btn-wrapper {
  @include mq(1024) {
    text-align: center;
  }
}
.feature-br-pc {
  @include mq(1299) {
    display: none;
  }
}
