@charset "utf-8";

.header-logo {
  width: 40px;
  height: 40px;
  @include mq(767) {
    width: 30px;
    height: 30px;
  }
  &-layout {
    margin-right: 20px;
    @include mq(767) {
      margin-right: 2vw;
    }
  }
}
.js-header-logo01 {
  display: none;
  &.is-show {
    display: block;
  }
}
.js-header-logo02 {
  display: none;
  &.is-show {
    display: block;
  }
}
