@charset "utf-8";
@import '../setting/color';
$hamburger-color01: #fff;
$hamburger-color02: #000;
/* ------------------------- */
/* ハンバーガーメニュー */
/* ------------------------- */
// ----- ドロワー表示背景 ----- //
.drawer-background {
  width: 100vw;
  height: 100vh;
  background-color: $hamburger-color02;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  z-index: map-get($layer, 'drawer') - 1;
  transition: 0.3s;
}

// ----- ドロワーメニュー内容 ----- //
.sp-global-menu {
  writing-mode: vertical-rl;
  width: 80%;
  height: 100vh;
  background-color: $hamburger-color02;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  transform: translateX(100%);
  z-index: map-get($layer, 'drawer');
  transition: 0.3s;
}

.is-drawerActive {
  .sp-global-menu {
    visibility: visible;
    transform: translateX(0px);
    opacity: 0.8;
  }
  .drawer-background {
    visibility: visible;
    opacity: 0.6;
  }
}
@include mq(767) {
  .hamburger-nav {
    width: 100%;
    position: relative;
    &-list {
      position: absolute;
      top: 30%;
      left: 50%;
      transform: translateX(-50%);
    }
    &-item {
      margin-left: 7vw;
      &:last-of-type {
        margin-left: 0;
      }
    }
    &-link {
      @include font-size(20);
      letter-spacing: 10px;
      font-family: $font-base;
      color: $hamburger-color01;
    }
  }
}
