@charset "utf-8";
@import '../setting/color';
.lower-page-ttl {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  text-align: center;
  font-weight: normal;
  &__layout {
    margin-top: 90px;
    margin-bottom: 78px;
    padding-right: 2vw;
    padding-left: 2vw;
  }
}
.lower-page-item {
  position: relative;
  display: flex;
  padding-top: 50px;
  padding-left: 360px;
  @include mq(767) {
    display: block;
    padding-left: 0;
    padding-top: 240px;
  }
  &__reverse {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    padding-top: 50px;
    padding-right: 360px;
    @include mq(767) {
      display: block;
      padding-right: 0;
      padding-top: 240px;
    }
  }
}

.lower-page-img-wrapper {
  position: relative;
  position: absolute;
  top: 0;
  left: 0;
  width: 525px;
  height: 300px;
  overflow: hidden;
  @include mq-m(768, 960) {
    width: 50%;
    height: 300px;
  }
  @include mq(767) {
    width: 100%;
  }
  &__reverse {
    position: relative;
    position: absolute;
    top: 0;
    right: 0;
    width: 525px;
    height: 300px;
    @include mq-m(768, 960) {
      width: 50%;
      height: 300px;
    }
    @include mq(767) {
      width: 100%;
    }
  }
}
.lower-page-img {
  height: 100%;
  position: absolute;
  top: -30px;
  overflow: hidden;
}
.lower-page-body {
  background-image: url('../img/Medium_bg.jpg');
  width: 813px;
  height: 381px;
  padding-top: 60px;
  padding-left: 240px;
  padding-right: 60px;
  margin-bottom: 84px;
  @include mq-m(768, 960) {
    padding-top: 30px;
    padding-left: 14vw;
    padding-right: 4vw;
  }
  @include mq(767) {
    width: 100%;
    padding-left: 4vw;
    padding-right: 4vw;
    margin-bottom: 0;
  }
  &__reverse {
    background-image: url('../img/Medium_bg.jpg');
    width: 813px;
    height: 381px;
    padding-top: 60px;
    padding-left: 60px;
    padding-right: 208px;
    margin-bottom: 84px;
    padding-top: 60px;
    @include mq-m(768, 960) {
      padding-top: 30px;
      padding-right: 14vw;
      padding-left: 4vw;
    }
    @include mq(767) {
      width: 100%;
      padding-right: 4vw;
      padding-left: 4vw;
      margin-bottom: 0;
    }
  }
}
.lower-page-heading {
  @include font-size(24);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(767) {
    @include font-size(20);
    text-align: center;
  }
  &__layout {
    margin-bottom: 48px;
    @include mq(767) {
      margin-bottom: 20px;
    }
  }
}
.lower-page-txt {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  &__layout {
    margin-bottom: 44px;
    @include mq(767) {
      margin-bottom: 20px;
    }
  }
}
.lower-page-caution {
  @include font-size(14);
  line-height: 21px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
}
