@charset "utf-8";
@keyframes header {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.header {
  display: none;
  width: 100%;
  height: 80px;
  z-index: map-get($layer, 'header');
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  &.is-show {
    display: block;
    animation: header 0.3s linear 0s;
  }
  &-inner {
    width: 100%;
    margin: 0 auto;
    padding: 13px 20px;
    display: flex;
    align-items: center;
    @include mq(767) {
      padding: 10px 2vw;
    }
  }
}
