@charset "utf-8";
.modal {
  display: none;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: map-get($layer, 'modal');
}
.modal__background {
  background-color: rgba(0, 0, 0, 0.8);
  height: 100vh;
  width: 100%;
}
.modal-content {
  z-index: map-get($layer, 'modal');
  position: relative;
  &__layout {
    width: 550px;
    height: 600px;
    padding: 29px 3.1% 90px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include mq(767) {
      width: 80%;
    }
    @include mq(425) {
      width: 80%;
      height: 90%;
    }
    @include mq(374) {
      height: 93%;
    }
  }
}
.modal-img-close {
  position: absolute;
  top: 20px;
  right: 22px;
  width: 18px;
  height: 18px;
}
.reservation-ttl {
  @include font-size(20);
  text-align: center;
  line-height: 20px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  &__layout {
    display: block;
    margin-bottom: 33.5px;
    padding-bottom: 29.5px;
    border-bottom: 1px solid #e7e7e7;
  }
}
.form-label {
  display: block;
  @include font-size(16);
  line-height: 16px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  &__layout {
    margin-top: 14px;
    margin-bottom: 14px;
  }
}
.txt-form {
  @include font-size(14);
  line-height: 14px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: #797878;
  font-weight: normal;
  &__layout {
    display: block;
    width: 100%;
    height: 32px;
    padding: 5px 10px;
    border: 1px solid #e7e7e7;
    background-color: #fff;
  }
}
.flatpickr-wrapper {
  width: 100%;
}
