@charset "utf-8";
@import '../setting/color';
.header-btn {
  width: 170px;
  height: 54px;
  background-color: #978f10;
  color: $color01;
  @include font-size(16);
  font-family: $font-base;
  text-align: center;
  margin-left: auto;
  padding-top: 16px;
  padding-bottom: 16px;
  @include mq(767) {
    width: 100px;
    height: 40px;
    @include font-size(12);
    padding-top: 12px;
    padding-bottom: 12px;
    margin-left: auto;
    margin-right: auto;
  }
  &:focus,
  &:hover {
    opacity: 0.8;
    border: 1px solid #978f10;
  }
  &-img {
    width: 22px;
    height: 22px;
    margin-right: 10px;
    @include mq(767) {
      width: 16px;
      height: 16px;
    }
  }
}
