@charset "utf-8";
@import '../setting/color';
.info-heading {
  @include font-size(36);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  text-align: center;
  @include mq(767) {
    @include font-size(24);
  }
  &__layout {
    margin-top: 20px;
  }
}
.info-tab {
  width: 165px;
  display: flex;
  margin-right: auto;
  margin-left: auto;
  margin-top: 46px;
  margin-bottom: 49px;
}
.tab.is-active {
  border-bottom: 1px solid red;
}
.info-tab-ttl {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  &__layout {
    margin-right: 53px;
    &:last-of-type {
      margin-right: 0;
    }
    &:focus,
    &:hover {
      cursor: pointer;
      opacity: 0.7;
    }
  }
}
// ----- タブ（営業情報）-----//
@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.info-content {
  display: none;
  width: 100%;
  &.is-show {
    display: block;
    animation: show 0.3s linear 0s;
  }
  @include mq-m(768, 1180) {
    padding-right: 10vw;
    padding-left: 10vw;
  }
  @include mq(767) {
    padding-right: 2vw;
    padding-left: 2vw;
  }
}
.info-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  @include mq(1180) {
    display: block;
  }
}
.info-item {
  width: 48.2%;
  height: 130px;
  padding: 15px 0 15px 10px;
  display: flex;
  background-color: #f6f6f6;
  margin-right: 40px;
  margin-bottom: 20px;
  &:nth-of-type(2n) {
    margin-right: 0;
    transition: 0.5s;
  }
  @include mq(1180) {
    width: 100%;
    margin-right: 0;
  }
}
.info-img-wrapper img {
  width: 103px;
  height: 100px;
}
.info-body {
  margin-left: 30px;
  @include mq(767) {
    margin-left: 0;
    padding-right: 2vw;
    padding-left: 2vw;
  }
}
.info-date {
  @include font-size(14);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
}
.info-txt {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  @include mq(767) {
    line-height: 20px;
  }
}
// ----- タブ（その他）-----//
.info-content__other {
  display: none;
  width: 100%;
  &.is-show {
    display: block;
    animation: show 0.3s linear 0s;
  }
  @include mq(1180) {
    padding-right: 10vw;
    padding-left: 10vw;
  }
}
.info-other {
  width: 100%;
  height: 450px;
  padding: 100px 2vw;
  background-color: #f6f6f6;
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.5);
}
.info-other-txt {
  @include font-size(40);
  line-height: 70px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: #666;
  text-align: center;
  word-break: keep-all;
  @include mq(767) {
    @include font-size(20);
    line-height: 40px;
  }
}
.info-br-sp {
  display: none;
  @include mq(450) {
    display: block;
  }
}
