@charset "utf-8";
.site-ttl {
  @include font-size(32);
  line-height: 48px;
  font-family: 'Toppan Bunkyu Midashi Mincho';
  letter-spacing: 5.76px;
  color: $color01;
  font-weight: bold;
  @include mq(767) {
    @include font-size(20);
    letter-spacing: 2px;
  }
  &.black {
    color: $color02;
  }
}
