@charset "utf-8";
// ----------大きさの定義---------------- //
.section-wrapper {
  width: 100%;
}
// .message-wrapper {
//   margin: 0 auto;
// }
// ----------inner幅-------------------- //
.section-inner {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
}
.footer-inner {
  width: 100%;
  margin: 0 auto;
  padding-top: 64px;
  padding-bottom: 61px;
}
.feature-inner {
  width: 100%;
  padding-top: 106px;
  padding-bottom: 78px;
}
.plan-inner {
  padding-top: 60px;
  width: 100%;
}
.info-inner {
  padding-top: 60px;
  padding-bottom: 75px;
}
.access-inner {
  padding-top: 60px;
  padding-bottom: 110px;
  @include mq(1180) {
    padding-right: 2vw;
    padding-left: 2vw;
  }
}
.cuisine-inner {
  padding-bottom: 66px;
  @include mq(767) {
    padding-bottom: 0;
  }
}
// ----------重なり順-------------------- //
$layer: (
  modal: 100,
  drawer: 40,
  footer-menu: 35,
  floating: 30,
  // トップへ戻る
  header: 20,
  default: 1,
);
