@charset "utf-8";
@import '../setting/color';
.spa-feature-content {
  width: 100%;
  border-top: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
  padding-top: 95.5px;
  padding-bottom: 74px;
  margin-top: 70.5px;
  margin-bottom: 96.5px;
  @include mq(767) {
    padding-top: 20px;
    border: none;
    padding-top: 0;
    padding-bottom: 0;
    padding: 0 2vw;
  }
}
.horizontal-table-ttl {
  @include font-size(26);
  line-height: 26px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  text-align: center;
  &__layout {
    margin-bottom: 51.5px;
  }
}
.horizontal-table-inner {
  width: 880px;
  margin-right: auto;
  margin-left: auto;
  @include mq(960) {
    width: 98%;
  }
}
.horizontal-table-header {
  @include font-size(15);
  line-height: 50px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  &__layout {
    width: 220px;
    border-top: 1px solid #e7e7e7;
    &:last-of-type {
      border-bottom: 1px solid #e7e7e7;
    }
    @include mq(720) {
      width: 23vw;
      @include font-size(14);
    }
  }
}
.horizontal-table-txt {
  @include font-size(15);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(720) {
    @include font-size(14);
    line-height: 22px;
    padding-top: 14px;
    padding-bottom: 14px;
  }
  &__multiple-row {
    line-height: 22px;
    padding-top: 14px;
    padding-bottom: 14px;
  }
  &__layout {
    padding-top: 9px;
    padding-bottom: 9px;
    border-top: 1px solid #e7e7e7;
    &:last-of-type {
      border-bottom: 1px solid #e7e7e7;
    }
  }
}
