@charset "utf-8";
.main-visual {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  z-index: 9;

  // -------- 各スライド --------//
  &-item {
    overflow: hidden;
    // 拡大した画像がはみ出ないようにする
    img {
      width: 100%;
      height: 100vh;
      object-fit: cover;
    }
  }
}

// ------ キャッチコピー --------//
.swiper-container-mv {
  position: relative;
}
.main-visual-copy-body {
  // height: 292px;
  writing-mode: vertical-rl;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translateY(-50%);
  z-index: 1;
  @include mq(767) {
    height: 170px;
  }
}
.main-visual-copy {
  @include font-size(34);
  font-weight: bold;
  letter-spacing: 0px;
  line-height: 34px;
  font-family: $font-base;
  color: $color01;
  @include mq(767) {
    @include font-size(18);
  }
}
.main-visual-copy01 {
  margin-left: 2.7vw;
}

// ----------swiper付属のcssをアレンジ----------//
.swiper-container-mv .slide-img img {
  display: block;
}

.swiper-container-mv .swiper-slide-active .slide-img,
.swiper-container-mv .swiper-slide-duplicate-active .slide-img,
.swiper-container-mv .swiper-slide-prev .slide-img {
  animation: fadezoom 5s 0s forwards;
}
@keyframes fadezoom {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1); // 拡大率はお好きなように
  }
}
