@charset "utf-8";
html {
  font-size: 100%;
  scroll-behavior: smooth;
}
body {
  &.is-no-scroll,
  &.is-drawerActive {
    overflow: hidden;
  }
}
/* btn-reset */
.btn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
}

.br-pc {
  @include mq(767) {
    display: none;
  }
}
.br-sp-none {
  @include mq(1024) {
    display: none;
  }
}

