@charset "utf-8";
@import '../setting/color';
.first-visual {
  width: 100%;
}
.first-visual-wrapper {
  width: 100%;
  height: 500px;
  position: relative;
}
.first-visual-img {
  width: 100%;
  height: 500px;
  object-fit: cover;
}
.first-visual-copy {
  @include font-size(34);
  line-height: 42px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color01;
  font-weight: bold;
  &__layout {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
