@charset "utf-8";
@import '../setting/color';
.plan-background {
  background-image: url(../../img/Medium_bg02.jpg);
}
.plan-content {
  width: 100%;
  margin-top: 66px;
  padding-right: 2vw;
  padding-left: 2vw;
  padding-bottom: 144px;
  @include mq(1150) {
    padding-bottom: 20px;
  }
}
.plan-list {
  max-width: 1180px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: auto;
  margin-left: auto;
  @include mq(1150) {
    display: block;
    width: 100%;
  }
}
.plan-img-wrapper img {
  width: 331px;
  height: 209px;
  @include mq(1150) {
    width: 100%;
    height: auto;
  }
}
.section-logo {
  width: 40px;
  height: 40px;
  &__layout {
    display: block;
    margin: 0 auto;
  }
}
.plan-heading {
  @include font-size(36);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  text-align: center;
  &__layout {
    margin-top: 20px;
  }
  @include mq(767) {
    @include font-size(24);
  }
}
.plan-item {
  width: 331px;
  margin-right: 5vw;
  &:last-of-type {
    margin-right: 0;
  }
  @include mq(1150) {
    width: 100%;
  }
  @include mq(767) {
    padding-right: 2vw;
  }
}
.plan-body {
  padding-top: 32px;
  @include mq(1150) {
    padding-top: 10px;
    padding-bottom: 50px;
  }
}
.plan-ttl {
  @include font-size(20);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  @include mq(767) {
    @include font-size(18);
    line-height: 24px;
  }
  &__layout {
    margin-bottom: 36px;
    @include mq(1150) {
      margin-bottom: 10px;
    }
  }
}
.plan-txt {
  @include font-size(16);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  @include mq(767) {
    @include font-size(14);
    line-height: 20px;
  }
}
.plan-br-pc {
  display: none;
}
