@charset "utf-8";
@import '../setting/color';
.footer-background {
  background-image: url(../img/Medium_bg3.jpg);
}
.footer-menu {
  margin-bottom: 96px;
}
.footer-list {
  display: flex;
  justify-content: center;
}
.footer-item {
  margin-right: 27px;
  writing-mode: vertical-rl;
  &:last-of-type {
    margin-right: 0;
  }
}
.footer-menu-txt {
  @include font-size(18);
  line-height: 32px;
  letter-spacing: 4px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
}
.footer-ttl {
  @include font-size(32);
  line-height: 32px;
  letter-spacing: 0px;
  font-family: 'Toppan Bunkyu Midashi Mincho';
  color: $color02;
  text-align: center;
  font-weight: bold;
  &__layout {
    margin-top: 20px;
    margin-bottom: 72px;
  }
}
.footer-address-body {
  display: flex;
  justify-content: center;
  margin-bottom: 23px;
}
.footer-tel-fax-body {
  display: flex;
  justify-content: center;
}
.footer-post {
  @include font-size(16);
  line-height: 16px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(375) {
    @include font-size(14);
  }
  &__layout {
    margin-right: 21px;
  }
}
.footer-address {
  @include font-size(16);
  line-height: 16px;
  letter-spacing: 2.88px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(375) {
    @include font-size(14);
  }
}
.footer-tel {
  @include font-size(16);
  line-height: 16px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(375) {
    @include font-size(14);
  }
  &__layout {
    margin-right: 20px;
  }
}
.footer-fax {
  @include font-size(16);
  line-height: 16px;
  letter-spacing: 0px;
  font-family: $font-base;
  color: $color02;
  font-weight: normal;
  @include mq(375) {
    @include font-size(14);
  }
}
.footer-small {
  color: $color01;
  @include font-size(12);
  line-height: 60px;
  letter-spacing: 0px;
  font-family: $font-base;
  font-weight: normal;
  text-align: center;
  &__layout {
    width: 100%;
    height: 60px;
    background-color: #000;
  }
}
